import React from 'react';
import './App.css';
import reactDom from 'react-dom';
import Ingate from './InGate';
import OutGate from './outGate';
import Rectify from './rectify';




const SquareBtn = (props) => {



  const handelClick = (e) =>{

    let btnvalue = props.btnVal
     
    document.getElementById('options').style.visibility = "hidden";
    document.getElementById('display').style.visibility = "visible";

    if (btnvalue == 1 ){


     reactDom.render(  <Ingate/>, document.getElementById('display') )

     
   
    }else if (btnvalue == 2){

      reactDom.render(  <OutGate/>, document.getElementById('display') )
    }
    
    else if (btnvalue == 3){
      
      reactDom.render(  <Rectify/>, document.getElementById('display') )
    
    
    }



  }

  return (  
 
    <button onClick= {handelClick} > {props.name}  </button>

  );
}
 
export default SquareBtn;