
import React from  'react';
import fire from './config'

class CurrentCounter3 extends React.Component  {
  
    constructor(props) {
        
        super(props);

     
        let ref = fire.database().ref("kop").orderByKey().limitToLast
  
       
        

        this.state = { 

            counterValue : 0
  
         }

         this.handleClick = this.handleClick.bind(this);
         this.handleClick2 = this.handleClick2.bind(this);

    
    }




 

 
 





    handleClick() {

        let b = this.state.counterValue + 1 
        this.setState({counterValue : b },()=>{

          fire.database().ref("currentCount").set(b)

        })
       
      

      }
   

      handleClick2() {

        let b = this.state.counterValue  - 1
         
        this.setState({counterValue : b },()=>{

          fire.database().ref("currentCount").set(b)

        })




      }


      componentDidMount(){


        fire.database().ref("currentCount").on("value",snap =>{

            console.log(snap.val())
      
            this.setState({
      
              counterValue : snap.val()
      
            })
      
      
      
      
          })
        




      }


   
   
   
    render() { 
        return (


            <div    
        
            style={
                    
                {   
                backgroundColor : "red",
                textAlign: "center",
                background: "dodgerblue",
                width  : 400,
                height: 120,
              
                }   
                } 
    
                id="counterDisp"
            >
                
    <h6  align="center"  > Current Count </h6>
    
    <h2 align="center" > {this.state.counterValue} </h2>
    
    <button onClick={this.handleClick} > + </button> 


    <br></br>
    <button onClick={this.handleClick2} > - </button> 
   
     
     
     </div>




          );
    }
}
 
export default CurrentCounter3;






